import axios from "axios";

export function transliterateToLatin(word) {
  var answer = "",
    a = {};

  // kirill to latin
  a["А"] = "A";
  a["а"] = "a";
  a["Б"] = "B";
  a["б"] = "b";
  a["В"] = "V";
  a["в"] = "v";
  a["Г"] = "G";
  a["г"] = "g";
  a["Д"] = "D";
  a["д"] = "d";
  a["Е"] = "E";
  a["е"] = "e";
  a["Ё"] = "Yo";
  a["ё"] = "yo";
  a["Ж"] = "J";
  a["ж"] = "j";
  a["З"] = "Z";
  a["з"] = "z";
  a["И"] = "I";
  a["и"] = "i";
  a["Й"] = "Y";
  a["й"] = "y";
  a["К"] = "K";
  a["к"] = "k";
  a["Л"] = "L";
  a["л"] = "l";
  a["М"] = "M";
  a["м"] = "m";
  a["Н"] = "N";
  a["н"] = "n";
  a["О"] = "O";
  a["о"] = "o";
  a["П"] = "P";
  a["п"] = "p";
  a["Р"] = "R";
  a["р"] = "r";
  a["С"] = "S";
  a["с"] = "s";
  a["Т"] = "T";
  a["т"] = "t";
  a["У"] = "U";
  a["у"] = "u";
  a["Ф"] = "F";
  a["ф"] = "f";
  a["Х"] = "X";
  a["х"] = "x";
  a["Ц"] = "Ts";
  a["ц"] = "ts";
  a["Ч"] = "Ch";
  a["ч"] = "ch";
  a["Ш"] = "Sh";
  a["ш"] = "sh";
  a["ъ"] = "'";
  a["Э"] = "E";
  a["э"] = "e";
  a["Ю"] = "Yu";
  a["ю"] = "yu";
  a["Я"] = "Ya";
  a["я"] = "ya";
  a["Ў"] = "O'";
  a["ў"] = "o'";
  a["Қ"] = "Q";
  a["қ"] = "q";
  a["Ғ"] = "G'";
  a["ғ"] = "g'";
  a["Ҳ"] = "H";
  a["ҳ"] = "h";

  for (let i in word) {
    if (word.hasOwnProperty(i)) {
      if (a[word[i]] === undefined) {
        answer += word[i];
      } else {
        answer += a[word[i]];
      }
    }
  }
  return answer;
}

export function transliterateToKiril(word) {
  var answer = "",
    a = {};

  // latin to kirill
  a["Ts"] = "Ц";
  a["ts"] = "ц";
  a["Ch"] = "Ч";
  a["ch"] = "ч";
  a["Sh"] = "Ш";
  a["sh"] = "ш";
  a["Yu"] = "Ю";
  a["yu"] = "ю";
  a["Ya"] = "Я";
  a["ya"] = "я";
  a["O'"] = "Ў";
  a["o'"] = "ў";
  a["Yo"] = "Ё";
  a["yo"] = "ё";
  a["G'"] = "Ғ";
  a["g'"] = "ғ";
  a["A"] = "А";
  a["a"] = "а";
  a["B"] = "Б";
  a["b"] = "б";
  a["V"] = "В";
  a["v"] = "в";
  a["G"] = "Г";
  a["g"] = "г";
  a["D"] = "Д";
  a["d"] = "д";
  a["E"] = "Е";
  a["e"] = "е";
  a["J"] = "Ж";
  a["j"] = "ж";
  a["Z"] = "З";
  a["z"] = "з";
  a["I"] = "И";
  a["i"] = "и";
  a["Y"] = "Й";
  a["y"] = "й";
  a["K"] = "К";
  a["k"] = "к";
  a["L"] = "Л";
  a["l"] = "л";
  a["M"] = "М";
  a["m"] = "м";
  a["N"] = "Н";
  a["n"] = "н";
  a["O"] = "О";
  a["o"] = "о";
  a["P"] = "П";
  a["p"] = "п";
  a["R"] = "Р";
  a["r"] = "р";
  a["S"] = "С";
  a["s"] = "с";
  a["T"] = "Т";
  a["t"] = "т";
  a["U"] = "У";
  a["u"] = "у";
  a["F"] = "Ф";
  a["f"] = "ф";
  a["X"] = "Х";
  a["x"] = "х";
  a["'"] = "ъ";
  a["Q"] = "Қ";
  a["q"] = "қ";
  a["H"] = "Ҳ";
  a["h"] = "ҳ";
  let wordArr = [];
  for (let i in word) {
    if (word.hasOwnProperty(i)) {
      if (a[word[i]] === undefined) {
        // answer += word[i];
        wordArr.push(word[i]);
      } else {
        console.log(a[word[i - 1]] + a[word[i]]);
        if (a[word[i - 1] + word[i]]) {
          // answer += a[word[i - 1] + word[i]];
          wordArr.pop();
          wordArr.push(a[word[i - 1] + word[i]]);
        } else {
          // answer += a[word[i]];
          wordArr.push(a[word[i]]);
        }
      }
    }
  }
  answer = wordArr.join("");
  return answer;
}

export const getCheckedWords = (words) => async () => {
  const { data } = await axios.post("http://localhost:8000/check/", {
    data: words,
  });

  localStorage.setItem("arrayOfWords", JSON.stringify(data));
};
